const demo = [{
	username: "昵称1",
	userpic: "../../static/default.jpg",
	newstime: "2019-10-20 下午04:30",
	isFollow: true,
	title: "我是标题",
	titlepic: "../../static/bgimg/1.jpg",
	support: {
		// 顶操作 type 为 "support",
		// 踩操作 type 为 "unsupport",
		// 未操作 type 为 ""
		type: "",
		support_count: 0,
		unsupport_count: 0
	},
	comment_count: 2,
	share_num: 2
}, {
	username: "昵称2",
	userpic: "../../static/default.jpg",
	newstime: "2019-10-20 下午04:30",
	isFollow: true,
	title: "我是标题",
	titlepic: "",
	support: {
		type: "",
		support_count: 1,
		unsupport_count: 2
	},
	comment_count: 2,
	share_num: 2
}, {
	username: "昵称3",
	userpic: "../../static/default.jpg",
	newstime: "2019-10-20 下午04:30",
	isFollow: true,
	title: "我是标题",
	titlepic: "",
	support: {
		type: "",
		support_count: 1,
		unsupport_count: 2
	},
	comment_count: 2,
	share_num: 2
}];
import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import commonList from '@/components/common/common-list.vue';
import loadMore from '@/components/common/load-more.vue';
import hotCate from '@/components/news/hot-cate.vue';
export default {
	data() {
		return {
			scrollH: 500,
			tabIndex: 0,
			tabBars: [{
				name: "关注"
			}, {
				name: "话题"
			}],
			// 关注列表
			list: [],
			// 1.上拉加载更多 2.加载中... 3.没有更多了
			loadmore: "上拉加载更多",
			hotCate: [{
				name: "关注"
			}, {
				name: "推荐"
			}]
		}
	},
	methods: {
		// 打开发布页
		openAddInput() {
			uni.navigateTo({
				url: '../add-input/add-input'
			});
		},
		// 切换选项卡
		changeTab(index) {
			this.tabIndex = index
		},
		// 监听滑动
		onChangeTab(e) {
			this.changeTab(e.detail.current)
		},
		// 顶踩操作
		doSupport(e) {
			// {type:"support", index:0}
			// 拿到当前对象
			let item = this.list[e.index]
			let msg = e.type === 'support' ? '顶' : '踩'
			// 之前没有操作过
			if (item.support.type === '') {
				item.support[e.type + '_count']++;
			} else if (item.support.type === 'support' && e.type === 'unsupport') { // 之前顶了
				// 顶 - 1
				if (item.support.support_count > 0) {
					item.support.support_count--;
				}
				// 踩 + 1
				item.support.unsupport_count++;
			} else if (item.support.type === 'unsupport' && e.type === 'support') { // 之前踩了
				// 踩 - 1
				if (item.support.unsupport_count > 0) {
					item.support.unsupport_count--;
				}
				// 顶 + 1
				item.support.support_count++;
			}
			item.support.type = e.type
			uni.showToast({
				title: msg + '成功'
			})
		},
		// 上拉加载更多
		loadmoreEvent(index) {
			// 验证当前是否处于可加载状态
			if (this.loadmore !== '上拉加载更多') return;
			// 设置加载状态
			this.loadmore = '加载中...'
			// 模拟请求数据
			setTimeout(() => {
				// 加载数据
				this.list = [...this.list, ...this.list]
				// 设置加载状态
				this.loadmore = '上拉加载更多'
			}, 2000)
		}
	},
	components: {
		uniNavBar,
		commonList,
		loadMore,
		hotCate
	},
	onLoad() {
		let res = uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight - res.statusBarHeight - 44
			}
		})
		// 加载测试数据
		this.list = demo;
	}
}
